import { decryption } from "../Encryption/decrypt";
import { DELETE, PATCH, POST } from "../UrlConfig/MethodType";

export const admissionActivateModuleFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v2/admission/ins/${data.instituteId}/admission/query`,
      method: POST,
      body: data.activateModule,
    }),
  });
};

export const admissionUpdateModuleFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v2/edit/staff/admission/staff/${data.osid}?nsid=${data.nsid}`,
      method: PATCH,
    }),
    invalidatesTags: ["Cash_finance"],
  });
};

// dashboard
export const admissionDashboardMasterFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v2/admission/${data.aid}/dashboard/query?mod_id=${data.mod_id}`,
    providesTags: ["Cash_finance"],
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response) => {
      let dect = decryption(response?.encrypt);
      return dect?.admission;
    },
  });
};

